import React, {useState, useEffect} from "react";
// import robots from "../robots"

function SearchBox (props) {
    const [roboName, setRoboName] = useState([]);

    useEffect(() => {
      fetch("https://jsonplaceholder.typicode.com/comments")
        .then((response) => response.json())
        .then((data) => setRoboName(data));
    }, []);

    
    function handleChange(event) {
        const robots = roboName.filter((x) =>
          x.email
            .substring(0, x.email.lastIndexOf("@"))
            .toLowerCase()
            .includes(event.target.value.toLowerCase())
        );

        props.filteredNames(robots)
    }

    
    return (
        <div>
            <input onChange={handleChange} className="pa3 ba b--green bg-lightest-blue " type="search" placeholder="Search RoboFriends" />
        </div>)
}

export default SearchBox;

