import React from "react";


function Robocard(props) {
    var roboUrl = "https://robohash.org/" + props.email + "?200*200"

    return (
      <div className="tc bg-light-green dib br3 pa3 ma2 grow bw2 shadow-5">
        <img src={roboUrl} alt="robo_img" />
        <h1>{props.email.substring(0, props.email.lastIndexOf("@"))}</h1>
        <p>{props.email}</p>
      </div>
    );
};

export default Robocard;


