import React, { useEffect, useState } from "react";
import Robocard from "./RoboCard";
import SearchBox from "./Searchbox"
import "tachyons";
import * as uuid from "uuid"
import Scroll from "./Scroll"


function App() {
  const [roboName, setRoboName] = useState([])

  function filteredRobots(names) {
    setRoboName(names)
  }

  useEffect( () => {
    fetch("https://jsonplaceholder.typicode.com/comments").then( response => response.json() ).then( data => {setRoboName(data)} );
  },[])



  
    return (
      <div className="tc">
        <h1>RoboFriends</h1>

        <SearchBox filteredNames={filteredRobots} names={roboName} />

        {roboName.length === 0 ? null : (
          <Scroll>
            {roboName.map((robos) => (
              <Robocard key={uuid.v4()} email={robos.email} />
            ))}
          </Scroll>
        )}
      </div>
    );
}

export default App;